export default{
	props:{
		item:Object,
		index:Number
	},
	methods:{
		// 打开个人空间
		openSpace() {
			console.log("打开个人空间");
		},
		follow(){
			// 通知父组件
			this.$emit('follow',this.index)
		},
		openDetail(){
			console.log("进入详情");
		},
		doSupport(type){
			this.$emit("doSupport",{
				type:type,
				index:this.index,
			})
			
		}
	}
}
